@import "styles/variables.scss";

.search {
    display: flex;
    flex-direction: column;
    padding-top: 36px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 60px;
    left: 0;
    background-color: $darkBlue;
    height: 210px;
    z-index: 100;
    box-sizing: border-box;
}

.search > .input > input {
    width: calc(100% - 40px);
    height: 90px;
    border: none;
    border-bottom: 1px solid $beige;
    background-color: $darkBlue;
    color: $beige;
    font-size: 80px;
    padding-left: 0;
}

.search > .input > input:focus-visible {
    outline: none;
}

.search > .input > input::placeholder {
    color: $beige;
    font-size: 80px;
    text-transform: uppercase;
}

.search > .input {
    display: flex;
    margin-left: 40px;
}

.search > .input *:last-child {
    right: 70px;
    top: 10px;
    cursor: pointer;
}

.closeBtn {
    position: absolute!important;
    top: 0;
    right: 30px;
    cursor: pointer;
}

@media #{$tablet} {
    .search {
        top: 0;
    }
    .search > .input {
        margin-left: 24px;
    }
    .search > .input > input {
        width: calc(100% - 24px);
        height: 60px;
        font-size: 32px;
        box-sizing: border-box;
    }
    .search > .input > input::placeholder {
        font-size: 32px;
        text-transform: capitalize;
    }
    .search > :last-child {
        position: absolute!important;
        top: 12px;
        right: 12px;
    }
}